import { FC, useState } from "react";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppGridContainer } from "../../@crema";
import { Box, Grid, TextField, Button } from "@mui/material";
import AppAnimate from "../../@crema/core/AppAnimate";
import UserTable from "./UserTable";
import { getUsersList, getFilterValues } from "../../redux/users/actions";
import "./style.css";

const Users = () => {
  const dispatch = useDispatch();
  const usersList = useSelector((state: any) => state.users.usersList);
  const filterValue = useSelector((state: any) => state.users.filterValues);
  const [status, setStatus] = useState<string>("Choose Status");
  const [filter, setFilter] = useState<string>("");
  const [search, setSearch] = useState<string>("");
  const [page, setPage] = useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = useState<number>(25);
  const [desiginationId, setDesiginationId] = useState<any>();

  let filterList: any = ["Choose Role"];
  filterValue &&  filterValue.map((value: any) => {
    filterList.push(value.designation_name);
  });
  useEffect(() => {
    dispatch(getFilterValues());
  }, []);

  useEffect(() => {
    filterValue && filterValue.map((value) => {
      if (value.designation_name == filter) {
        setDesiginationId(value.id);
      }
    });
  }, [filter]);

  useEffect(() => {
    dispatch(
      getUsersList({
        filter: desiginationId ? desiginationId : "",
        search: search,
        page: page + 1,
        rowsPerPage: rowsPerPage,
        status: status == "Choose Status"  ? '' : (status == "Active" ? 1 :0),
      })
    );
    console.log("filter", desiginationId);
    console.log("search", search);
    console.log("page", page);
    console.log("rowsPerPage", rowsPerPage);
    console.log("status", status);
  }, [search, page, rowsPerPage, status,desiginationId]);


  return (
    <>
    <div style={{padding:'20px'}}>
    <AppAnimate animation="transition.slideUpIn" delay={200}>
     
        <Box>
           <Grid  lg={3} sm={6} xs={12} mb={6}>
           <h3 style={{ paddingTop: "10px" }}>
           User Management
          </h3>
        </Grid>
          
          <AppGridContainer>
            <Grid item xs={12} md={12}>
              <UserTable
                filterValue={filterList}
                usersList={usersList}
                page={page}
                setPage={setPage}
                rowsPerPage={rowsPerPage}
                setRowsPerPage={setRowsPerPage}
                search={search}
                setSearch={setSearch}
                filter={filter}
                setFilter={setFilter}
                status={status}
                setStatus={setStatus}
              />
            </Grid>
          </AppGridContainer>
        </Box>
      </AppAnimate>
    </div>
  
    </>
  );
};

export default Users;
