import { FC, useState } from "react";
import React, { useEffect } from "react";
import Table from "@mui/material/Table";
import TableHead from "@mui/material/TableHead";
import TableBody from "@mui/material/TableBody";
import TableHeading from "./tableHeading";
import TableItem from "./TableItem";
import TableFooter from "@mui/material/TableFooter";
import TablePagination from "@mui/material/TablePagination";
import { useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import FirstPageIcon from "@mui/icons-material/FirstPage";
import KeyboardArrowLeft from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRight from "@mui/icons-material/KeyboardArrowRight";
import LastPageIcon from "@mui/icons-material/LastPage";
import TableRow from "@mui/material/TableRow";
import TableContainer from "@mui/material/TableContainer";
import Paper from "@mui/material/Paper";
import { Grid, TextField, Button } from "@mui/material";
import AppSelect from "@crema/core/AppSelect";
import { useIntl } from "react-intl";
import { Fonts } from "../../../shared/constants/AppEnums";
import IntlMessages from "../../../@crema/utility/IntlMessages";
import { useHistory } from "react-router-dom";
interface TablePaginationActionsProps {
  count: number;
  page: number;
  rowsPerPage: number;
  onPageChange: (
    event: React.MouseEvent<HTMLButtonElement>,
    newPage: number
  ) => void;
}

function TablePaginationActions(props: TablePaginationActionsProps) {
  const theme = useTheme();
  const { count, page, rowsPerPage, onPageChange } = props;

  const handleFirstPageButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    onPageChange(event, 0);
  };

  const handleBackButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    onPageChange(event, page - 1);
  };

  const handleNextButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    onPageChange(event, page + 1);
  };

  const handleLastPageButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
  };

  return (
    <Box sx={{ flexShrink: 0, ml: 2.5 }}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        {theme.direction === "rtl" ? <LastPageIcon /> : <FirstPageIcon />}
      </IconButton>
      <IconButton
        onClick={handleBackButtonClick}
        disabled={page === 0}
        aria-label="previous page"
      >
        {theme.direction === "rtl" ? (
          <KeyboardArrowRight />
        ) : (
          <KeyboardArrowLeft />
        )}
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        {theme.direction === "rtl" ? (
          <KeyboardArrowLeft />
        ) : (
          <KeyboardArrowRight />
        )}
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="last page"
      >
        {theme.direction === "rtl" ? <FirstPageIcon /> : <LastPageIcon />}
      </IconButton>
    </Box>
  );
}

interface FuncProp {
  filterValue?:any;
  usersList?: any;
  filter?: string;
  setFilter?: any;
  search?: string;
  setSearch?: any;
  page?: any;
  setPage?: any;
  rowsPerPage?: any;
  setRowsPerPage?: any;
  setStatus?: any;
  status?: string;
}
const UserTable: FC<FuncProp> = ({
  filterValue,
  filter,
  setFilter,
  setSearch,
  search,
  page,
  setPage,
  rowsPerPage,
  setRowsPerPage,
  setStatus,
  status,
  usersList,
}) => {
  const handleChangePage = (
    event: React.MouseEvent<HTMLButtonElement> | null,
    newPage: number
  ) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setRowsPerPage(parseInt(event.target.value));
    setPage(0);
  };


  const history = useHistory();
  return (
    <TableContainer sx={{ borderRadius: "20px" }} component={Paper}>
      <Grid item xs={12} lg={12} style={{ padding: 20, display: "flex" }}>
     
        <Grid item lg={3} sm={6} xs={12} mb={6} mr={1}>
          <TextField
            className="textFieldDesign"
            fullWidth
            placeholder="Search Text"
            label="Search"
            value={search}
            onChange={(event: any) => {
              setSearch(event.target.value);
              setPage(0);
            }}
          />
        </Grid>

        <Grid style={{ textAlign: "start",display:'flex'}} item lg={4} sm={6} xs={12} mb={6}>
          <AppSelect
            menus={filterValue}
            defaultValue={filterValue[0]}
            onChange={(event: any) => {
              setFilter(event);
              setSearch("");
              setRowsPerPage(25);
            }}
          />
           <AppSelect
            menus={["Choose Status","Active", "InActive"]}
            defaultValue={"Choose Status"}
            onChange={(event: any) => {
              setStatus(event);
              setSearch("");
              setRowsPerPage(25);
            }}
          />
        </Grid>
        {/* <Grid style={{ textAlign: "start" }} item lg={2} sm={6} xs={12} mb={6}>
          <AppSelect
            menus={["Choose Status","Active", "InActive"]}
            defaultValue={"Choose Status"}
            onChange={(event: any) => {
              setStatus(event);
              setSearch("");
              setRowsPerPage(25);
            }}
          />
        </Grid> */}
        <Grid style={{ textAlign: "end" }} item lg={5} sm={6} xs={12} mb={6}>
          <Button variant="contained" color="success"
            onClick={() => {
              history.push("/admin/users-management/add-user");
            }}
          >
            Add User
          </Button>
        </Grid>
      </Grid>

      {usersList.data && usersList.data.length > 0 ? (
        <>
          <Table>
            <TableHead>
              <TableHeading />
            </TableHead>

            <TableBody>
              {usersList.data &&
                usersList.data.map((row: any) => (
                  <TableItem key={row.id} row={row} />
                ))}
            </TableBody>
            <TableFooter>
              <TableRow>
                <TablePagination
                  rowsPerPageOptions={[25, 50, 100]}
                  colSpan={7}
                  count={usersList && usersList.total ? usersList.total : 0}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  SelectProps={{
                    inputProps: {
                      "aria-label": "rows per page",
                    },
                    native: true,
                  }}
                  onPageChange={handleChangePage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActions}
                />
              </TableRow>
            </TableFooter>
          </Table>
        </>
      ) : (
        <>
          <p style={{ margin: "0px 20px 20px 20px", fontSize: "22px" }}>
            {" "}
            No Data Available
          </p>
        </>
      )}
    </TableContainer>
  );
};

export default UserTable;
